.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
  font-style: normal;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  height: 40px;
}

.header__navigation {
  display: flex;
}

.header__navigation-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.header__navigation-list > li:first-child a {
  margin-left: 0;
}

.header__navigation-link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: flex;
  position: relative;
}

.header__navigation-link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
}

.container {
  grid-gap: 46px 48px;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  flex-shrink: 0;
  width: 16px;
  height: 16px;
  margin-left: 4px;
}

.stars__star:first-child {
  margin-left: 0;
}

.stars__star:last-child {
  background-image: url("star.822d561e.svg");
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  font-size: 12px;
  line-height: 150%;
  transition: all .3s;
  display: flex;
}

.card * {
  color: #060b35;
  font-size: 12px;
  font-weight: 400;
}

.card__img {
  background-image: url("imac.202c52ff.jpeg");
  background-size: 160px 134px;
  width: 160px;
  height: 134px;
}

.card__content {
  margin-top: 40px;
}

.card__name {
  width: 100%;
  font-weight: 500;
  transition: all .3s;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 140%;
}

.card__reviews {
  justify-content: space-between;
  align-items: end;
  margin-top: 16px;
  display: flex;
}

.card__reviews-text {
  text-align: right;
  font-size: 10px;
  line-height: 140%;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.card__price-title {
  color: #616070;
}

.card__price-value {
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 112.5%;
}

.card__buy-button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 114.29%;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__name {
  color: #34568b;
}

html, body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

h1, h2, h3, p {
  margin: 0;
}

